.App {
  text-align: center;
}

.App-header {
  background-color: #000;
  min-height: 25vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  margin-bottom: 1rem;
}

a:not(header a):hover {
  color: cadetblue;
  transition: color 500ms;
}
header a, .darkMode a:not(form a) {
  color: #61dafb;
  transition: color 500ms;
}
header a:hover, .darkMode a:not(form a):hover {
  color: gold;
}

/* Bootstrap Card */
.card {
  box-sizing: content-box !important;
}

#avatar {
  height: 3rem;
  width: 3rem;
  transition: transform 400ms ease-out;
}
#avatar:hover {
  transform: scale(1.1);
}

/* Dark Mode Styles */
.darkMode, .darkMode #dialog-head, .darkMode #dialog-body, .darkMode .card {
  background-color: #000;
}
.darkMode, .darkMode li, .darkMode button, .darkMode h2 {
  color: #fff;
}
.darkMode .card, .darkMode #dialog-ctn {
  border: solid 2px #fff !important;
}
.darkMode .MuiPaper-root {
  box-shadow: 0px 11px 15px -7px rgb(255 255 255 / 20%), 0px 24px 38px 3px rgb(255 255 255 / 14%), 0px 9px 46px 8px rgb(255 255 255 / 12%);
}